<template>
    <div>
      <el-input v-model="value" placeholder="请选择弹窗" @click.native="openDialog"></el-input>

      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%"
        :append-to-body="true">
        <span>这是一段信息</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="okbtn">确 定</el-button>
        </span>
      </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "",
        props:['value'],
        data(){
          return {
            dialogVisible:false
          }
        },
      methods:{
        openDialog(){
          this.dialogVisible = true
        },
        okbtn(){
          this.dialogVisible = false
          this.$emit('input','lll')
        }
      }
    }
</script>

<style scoped>

</style>



